import React, { FunctionComponent } from "react";

import { Navbar, Nav, Button, Image } from "react-bootstrap";
import { Container, Row, Col } from "react-bootstrap";
import { Form, Modal, Alert } from "react-bootstrap";
import { OverlayTrigger, Tooltip } from "react-bootstrap";
import { Upload, Download, PlusCircle, Gear } from 'react-bootstrap-icons';

import { RecipeIndex } from "./service/RecipeDB";
import { RecipeService } from "./service/RecipeService";

import { PlanSettings } from "./model/PlanSettings";
import { Plan2 } from "./model/Plan2";

import { Recipe, serializeRecipes, deserializeRecipes } from "./model/Recipe";
import { RecipeSelector } from "./component/RecipeSelector";
import { LoadFileDialog } from "./component/LoadFileDialog";
import { SaveFileDialog } from "./component/SaveFileDialog";
import { PlanSettingsDialog } from "./component/PlanSettings";
import { Planner } from "./component/Planner";
import { RecipeEditor } from "./component/RecipeEditor";

import { exportPlan, exportShoppingList } from "./PlanExporter";

import { Beforeunload } from "react-beforeunload";

import "bootstrap/dist/css/bootstrap.min.css";

function getVersionNumber() {
  return `${process.env.REACT_APP_VERSION}`;
}

const Menu: FunctionComponent = () => {
  return (
    <Navbar bg="mealee-menu" variant="dark" className="pl-1">
      <Navbar.Brand className="logo" href="#home">Mealee</Navbar.Brand>
      <Navbar.Toggle aria-controls="basic-navbar-nav" />
      <Navbar.Collapse id="basic-navbar-nav">
        <Nav className="container-fluid">                
            <Nav.Item className="ml-auto">
                <Nav.Link>v{getVersionNumber()}</Nav.Link>
            </Nav.Item>
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  );
};

type IconBarProps = {
    onLoadFile: () => void;
    onNewRecipe: () => void;
    onSaveFile: () => void;
    onEditSettings: () => void;
    unsavedChanges: boolean;
  };  

const IconBar: FunctionComponent<IconBarProps> = ({
    onLoadFile,
    onNewRecipe,
    onSaveFile,
    onEditSettings,
    unsavedChanges
  }) => {
    return (
        <Container className="pr-0">
            <Row className="mb-4">
                <Col className="pr-0">
                <OverlayTrigger
                    key="upload"
                    placement="right"
                    overlay={
                        <Tooltip id="tooltip-upload">
                        Load recipes
                        </Tooltip>
                    }>
                    <Upload style={{color: "#ffffff",
                                    cursor: "pointer"}} onClick={onLoadFile}/>
                </OverlayTrigger> 
                </Col>
            </Row>
            <Row className="mb-4">
                <Col className="pr-0">
                <OverlayTrigger
                    key="download"
                    placement="right"
                    overlay={
                        <Tooltip id="tooltip-download">
                        Save recipes
                        </Tooltip>
                    }>
                    {unsavedChanges
                        ? <Download style={{color: "#ffffff",
                                            cursor: "pointer"}} onClick={onSaveFile}/>
                        : <Download style={{color: "#ffffff40"}}/>
                    } 
                </OverlayTrigger>                 
                </Col>
            </Row>
            <Row className="mb-4">
                <Col className="pr-0">
                <OverlayTrigger
                    key="new"
                    placement="right"
                    overlay={
                        <Tooltip id="tooltip-new">
                        Add new recipe
                        </Tooltip>
                    }>
                    <PlusCircle style={{color: "#ffffff",
                                        cursor: "pointer"}} onClick={onNewRecipe}/>
                    </OverlayTrigger>
                </Col>
            </Row>
            <Row className="mb-4">
                <Col className="pr-0">
                    <OverlayTrigger
                        key="new"
                        placement="right"
                        overlay={
                            <Tooltip id="tooltip-new">
                            Plan settings
                            </Tooltip>
                        }>

                        <Gear style={{color: "#ffffff",
                                    cursor: "pointer"}} onClick={onEditSettings}/>
                    </OverlayTrigger>
                </Col>
            </Row>
        </Container>
    );
};

type AppState = {
  recipeIndex: RecipeIndex;
  planSettings: PlanSettings;
  plan2: Plan2;
  showDialog?: Dialog;
  recipeToEdit?: Recipe;
  error: string | null;
  unsavedChanges: boolean;
};

enum Dialog {
  LoadFile,
  SaveFile,
  RecipeEditor,
  SavePlanXLS,
  ShoppingList,
  PlanSettings
}

class App extends React.Component<{}, AppState> {
  constructor(props: {}) {
    super(props);

    let recipeIndex = new RecipeIndex();
    let planSettings = new PlanSettings();

    // Hard binding is needed if `loadFile` do not use arrow notation
    // this.loadFile = this.loadFile.bind(this);
    this.state = {
      recipeIndex: recipeIndex,
      planSettings: planSettings,
      plan2: Plan2.new(planSettings.nrRecipes),
      showDialog: undefined,
      recipeToEdit: undefined,
      error: null,
      unsavedChanges: false,
    };
  }

  addRecipes = (newRecipes: Recipe[], fromFile = false) => {
    this.setState((st) => {
      // You want to save if it is not a first file load
      let needsSave =
        st.unsavedChanges || !(fromFile && st.recipeIndex.count() === 0);

      let ret = {};
      let newRecipeIndex = st.recipeIndex.addAll(newRecipes);

      if (newRecipeIndex !== st.recipeIndex) {
        ret = {
          recipeIndex: newRecipeIndex,
          unsavedChanges: needsSave,
        };
      }

      return ret;
    });
  };

  saveRecipe = (recipe: Recipe) => {
    this.addRecipes([recipe]);
    this.closeRecipeEditor();
  };

  onFileLoaded = (ev: ProgressEvent<FileReader>) => {
    let res = ev.target?.result;

    if (typeof res == "string") {
      try {
        this.addRecipes(deserializeRecipes(res), true);
      } catch (e) {
        this.setState({ error: e.message });
      }
    }
    this.closeDialog();
  };

  loadFile = (file: File) => {
    var reader = new FileReader();
    reader.onload = (ev) => this.onFileLoaded(ev);
    reader.readAsText(file, "UTF-8");
  };

  genRecipeFileContent = () => {
    return serializeRecipes(this.state.recipeIndex.all());
  };

  fileSaved = () => {
    this.setState({ unsavedChanges: false });
    this.closeDialog();
  };

  showDialog = (dialog: Dialog) => {
    return () => this.setState({ showDialog: dialog });
  };

  closeDialog = () => {
    this.setState({ showDialog: undefined });
  };

  closeRecipeEditor = () => {
    this.setState({ recipeToEdit: undefined, showDialog: undefined });
  };

  dismissError = () => {
    this.setState({ error: null });
  };

  updatePlanSettings = (ps: PlanSettings) => {
    this.setState((st) => {
      return {
        planSettings: ps,
        plan2: st.plan2.setNrBuckets(ps.nrRecipes)
      };
    });
  };

  updatePlan = (plan: Plan2) => {
    this.setState({
      plan2: plan
    });
  };

  editRecipe = (recipe: Recipe) => {
    this.setState({ showDialog: Dialog.RecipeEditor, recipeToEdit: recipe });
  };

  deleteRecipe = (recipe: Recipe) => {
    this.setState((st) => {
      let newRecipeIndex = st.recipeIndex.delete(recipe.getId());

      return {
        recipeIndex: newRecipeIndex,
        showDialog: undefined,
        recipeToEdit: undefined,
        unsavedChanges: true,
      };
    });
  };

  exportButtons = () => {
    if (this.state.plan2.getNrRecipes() > 0) {
      return (
        <Row>
          <Col className="pl-0">
            <Button
              variant="mealee"
              onClick={this.showDialog(Dialog.SavePlanXLS)}>
              Export plan &nbsp;
                <Image src="xlsx_logo.svg" height="18" />
            </Button>
            <Button
              className="ml-2"
              variant="mealee"
              onClick={this.showDialog(Dialog.ShoppingList)}>
              Show shopping list &nbsp;
                <Image src="todoist_logo.svg" height="18" />
            </Button>
          </Col>
        </Row>
      )
    }
    else {
      return <></>
    }
  }

  renderDialog = () => {
    switch (this.state.showDialog) {
      case Dialog.LoadFile: {
        return (
          <LoadFileDialog
            onClose={this.closeDialog}
            title="Load recipes file"
            loadButtonTitle="Load recipes"
            onLoad={this.loadFile}
          />
        );
      }

      case Dialog.SaveFile: {
        return (
          <SaveFileDialog
            onClose={this.closeDialog}
            title="Save recipes file"
            saveButtonTitle="Save recipes"
            data={this.genRecipeFileContent()}
            type="application/json"
            initialFileName="recipes"
            extension="json"
            onSave={this.fileSaved}
          />
        );
      }

      case Dialog.RecipeEditor: {
        return (
          <RecipeEditor
            onClose={this.closeRecipeEditor}
            onSave={this.saveRecipe}
            onDelete={this.deleteRecipe}
            recipe={this.state.recipeToEdit}
            recipeService={new RecipeService(this.state.recipeIndex)}
          />
        );
      }

      case Dialog.SavePlanXLS: {
        return (
          <SaveFileDialog
            onClose={this.closeDialog}
            title="Save plan"
            saveButtonTitle="Save plan"
            data={exportPlan(this.state.recipeIndex, this.state.plan2)}
            type="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
            initialFileName="plan"
            extension="xlsx"
            onSave={this.closeDialog}
          />
        );
      }

      case Dialog.ShoppingList: {
        return (
          <Modal show={true} onHide={this.closeDialog}>
            <Modal.Header closeButton>
              <Modal.Title>Shopping list</Modal.Title>
            </Modal.Header>

            <Modal.Body>
              <Form>
                <Form.Control
                  as="textarea"
                  rows={20}
                  cols={80}
                  value={exportShoppingList(this.state.recipeIndex, this.state.plan2)}
                  readOnly
                />
              </Form>
            </Modal.Body>

            <Modal.Footer>
              <Button variant="secondary" onClick={this.closeDialog}>
                Close
              </Button>
            </Modal.Footer>
          </Modal>
        );
      }
    
      case Dialog.PlanSettings: {
            return (
              <PlanSettingsDialog 
                onClose={this.closeDialog} 
                planSettings={this.state.planSettings}
                onUpdate={this.updatePlanSettings}/>
            );
      }

      default: {
        return <></>;
      }
    }
  };

  render() {
    return (
      <Container fluid className="d-flex flex-column vh-100">
        <Alert
          variant="danger"
          onClose={this.dismissError}
          show={!!this.state.error}
          dismissible
        >
          {this.state.error}
        </Alert>

        {this.state.unsavedChanges &&
            <Beforeunload onBeforeunload={() => "You'll lose your data!"} />
        }

        {this.renderDialog()}

        <Row>
          <Col>
            <Menu/>
          </Col>
        </Row>
        
            <Row className="h-100" style={{ minHeight: 0 }}>
            <Col xs="auto" className="h-100 pl-0 pr-0 mr-2 mt-3" style={{ minHeight: 0 }}>
                <IconBar
                    onLoadFile={this.showDialog(Dialog.LoadFile)}
                    onNewRecipe={this.showDialog(Dialog.RecipeEditor)}
                    onSaveFile={this.showDialog(Dialog.SaveFile)}
                    onEditSettings={this.showDialog(Dialog.PlanSettings)}
                    unsavedChanges={this.state.unsavedChanges}/>
            </Col>
            {this.state.recipeIndex.count() === 0 &&
            <Col xs="11" className="pt-3">
                <Alert key="recipe" variant="warning">
                There are no recipes. Use the icon bar on the left to load some or to add a new recipe.
                </Alert>
            </Col>
            }
            {this.state.recipeIndex.count() > 0 &&
            <>
            <Col xs="3" className="h-100" style={{ minHeight: 0 }}>

                <RecipeSelector
                    plan={this.state.plan2}
                    recipeIndex={this.state.recipeIndex}
                    onEdit={this.editRecipe}/>

            </Col>
            <Col>
                <Container className="pt-2 pl-0 pr-0">
                <Row>
                    <Col>
                        <Planner
                            recipeDB={this.state.recipeIndex}
                            plan={this.state.plan2}
                            updatePlan={this.updatePlan}/>
                    </Col>
                </Row>
                {this.exportButtons()}
                </Container>
            </Col>
            </>
            }
            </Row>
        
      </Container >
    );
  }
}

export default App;
